<template>
  <div class="app-container">
    <div class="filter-container">
      <el-input
        placeholder="用户名"
        v-model="listQuery.username"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-input
        placeholder="真实姓名"
        v-model="listQuery.name"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-button class="filter-item" type="primary" icon="el-icon-search" @click="searchData">搜索</el-button>
      <el-button
        class="filter-item"
        style="margin-left: 10px;"
        type="primary"
        icon="el-icon-edit"
        @click="addData()"
      >添加</el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="data"
      border
      fit
      highlight-current-row
      style="width: 100%;"
    >
      <el-table-column label="版本号" align="center" prop="tagName"/>
      <el-table-column label="创建时间" align="center" prop="createTime">
      <template slot-scope="scope">
            <el-tag type="warning">
              <i class="el-icon-time"></i>
              <span>{{ scope.row.createTime | parseTime('{y}-{m}-{d}') }}</span>
            </el-tag>
          </template>
      </el-table-column>
      <el-table-column label="创建人" align="center" prop="author"/>
      <el-table-column label="版本描述" align="center" prop="content"/>
      <el-table-column label="操作" align="center" width="300" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click="addData(scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" disabled @click="deleteData(scope.row._id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="listQuery.page"
      :limit.sync="listQuery.limit"
      @pagination="getData"
    />

    <el-dialog title="新增/编辑" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="editData"
        label-position="left"
        label-width="100px"
        style="width: 500px; margin-left:50px;"
      >
        <el-form-item label="版本号" prop="tagName">
          <el-input v-model="editData.tagName"/>
        </el-form-item>
        <el-form-item label="版本简介" prop="content">
          <el-input type="textarea" v-model="editData.content"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitData('dataForm')">确定</el-button>
        <el-button type="primary" @click="cancelEdit('dataForm')">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { Project } from "@/api/project";
import Pagination from "@/components/Pagination"; // Secondary package based on el-pagination
import { mapGetters } from "vuex";
export default {
  components: { Pagination },
  computed: {
    ...mapGetters(["name", "userId"])
  },
  data() {
    return {
      dialogFormVisible: false,
      rules: {
        content: [
          { required: true, message: "请填写版本简介", trigger: "blur" }
        ],
        tagName: [{ required: true, message: "请填写版本号", trigger: "blur" }],
      },
      listQuery: {
        name: null,
        username: null,
        page: 1,
        limit: 10
      },
      listLoading: false,
      total: 0,
      // 需要新增和编辑的数据对象
      editData: {
          _id:null,
        content: "",
        // 版本名
        tagName: ""
      },
      data: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    /**
     * @description 搜索数据
     * @function searchData
     * @return {type} {description}
     */
    searchData() {
      this.listQuery.page = 1;
      this.getData();
    },
    /**
     * @description 获取数据
     * @function getData
     * @return {type} {description}
     */
    getData() {
        this.listLoading=true;
      Project.getTag(this.listQuery).then(res => {
        this.data = res.data;
        this.total = res.total;
        this.listLoading=false;
      });
    },
    /**
     * @description 数据添加
     * @function addData
     * @return {type} {description}
     */
    addData(
      obj = {
        _id:null,
        content: "",
        tagName: ""
      }
    ) {
      this.editData = {
        _id: obj._id,
        content: obj.content,
        tagName: obj.tagName
      };
      this.dialogFormVisible = true;
    },
    /**
     * @description 提交数据
     * @function {function name}
     * @return {type} {description}
     */
    submitData(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (!this.editData._id) {
            Project.createTag({
                ...this.editData,
                author:this.name,
                userId:this.userId
            }).then(res => {
              this.$message.success("添加成功");
              this.getData();
              this.cancelEdit();
            });
          } else {
            Project.updateTag(this.editData).then(res => {
              this.$message.success("编辑成功");
              this.getData();
              this.cancelEdit();
            });
          }
        } else {
          return false;
        }
      });
    },
    /**
     * @description 数据重置
     * @function cancelEdit
     * @return {type} {description}
     */
    cancelEdit(formName = "dataForm") {
      this.$refs[formName].resetFields();
      this.editData = {
          id:null,
        content: "",
        tagName: ""
      };
      this.dialogFormVisible = false;
    },
    /**
     * @description 删除数据
     * @function deleteData
     * @return {type} {description}
     */
    deleteData(_id) {
      this.$confirm("确定删除该版本?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Project.deleteTag({
            _id
          }).then(res => {
            this.$message({
              type: "success",
              message: "成功删除"
            });
            this.getData();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};
</script>
